//上传图片接口请求
const uploadFile = (url,method,data,file) => {
	let token = uni.getStorageSync("token");
	let baseDefaultOpts = {
		url:baseUrl + url,
		filePath:file,
		formData:data,
		name:'file',
		method:method,
		header:{
			'token':token,// 'X-Requested-With': 'XMLHttpRequest',
			// "Content-Type": "multipart/form-data",
			// 'Content-Type' : 'application/x-www-form-urlencoded;charset=UTF-8',
			
		},
		//配置请求头
		dataType:'json',
	}
	let promise = new Promise(function(resolve,reject){
		uni.uploadFile(baseDefaultOpts).then(
			(response) => {
				const res = JSON.parse(response[1].data);
				resolve(res)
			}
		).catch(
			(response) => {
				reject(response)
			}
		)
	})
	return promise
}

export default{
	baseUrl,
	baseRequest,
	uploadFile
}

